import { App, inject, InjectionKey } from 'vue'
import FocusableCore from './core'
const tvKey = Symbol() as InjectionKey<FocusableCore>
const _focusableCore = new FocusableCore()

document.removeEventListener(
  'keydown',
  _focusableCore.onKeyDownEvent.bind(_focusableCore),
  false
)
document.addEventListener(
  'keydown',
  _focusableCore.onKeyDownEvent.bind(_focusableCore)
)
document.removeEventListener(
  'keyup',
  _focusableCore.onKeyUpEvent.bind(_focusableCore),
  false
)
document.addEventListener(
  'keyup',
  _focusableCore.onKeyUpEvent.bind(_focusableCore)
)

export default {
  install: (app: App) => {
    app.directive('focusable', {
      beforeMount (el) {
        if (!el) return
        el.setAttribute(_focusableCore.focusItemAttrname, '')
        const className = el.getAttribute('class') || ''
        className.split(' ').indexOf(_focusableCore.focusClassName) !== -1 &&
          el.setAttribute(_focusableCore.focusedAttrname, '')
      }
    })
    app.directive('focusable-scroll', {
      beforeMount (el) {
        if (!el) return
        el.setAttribute(_focusableCore.scrollItemAttrname, '')
      }
    })
    app.provide(tvKey, _focusableCore)
  }
}

const useTv = () => {
  return inject(tvKey, _focusableCore)
}

export { useTv }
