import { createApp } from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import tvFocusable from './plugins/focusable'

const vueApp = createApp(App)
vueApp
  .use(tvFocusable)
  .use(store)
  .use(router)

router.isReady().then(() => vueApp.mount('#app'))
