
import { defineComponent, ref, onMounted, nextTick } from 'vue'
import { useTv } from '@/plugins/focusable'
import { useRouter, useRoute } from 'vue-router'
export default defineComponent({
  name: 'App',
  setup () {
    const firstRef = ref()
    const $tv = useTv()
    const router = useRouter()
    const route = useRoute()
    const rMap = ref([
      {
        name: 'Example',
        text: '是否可聚焦'
      },
      {
        name: 'Example2',
        text: '事件'
      },
      {
        name: 'Example3',
        text: '滚动DIV'
      },
      {
        name: 'Example4',
        text: '弹出层'
      },
      {
        name: 'Example5',
        text: '自定义配置'
      },
      {
        name: 'Example6',
        text: '双路由'
      }
    ])
    const nowIndex = ref(rMap.value.findIndex(i => i.name === route.name))
    onMounted(() => {
      $tv.onFocus(firstRef.value[nowIndex.value])
    })
    const go = (name: string, i: number) => {
      router.push({ name })
      nowIndex.value = i
      nextTick(() => $tv.onFocus(firstRef.value[nowIndex.value]))
    }
    return {
      firstRef,
      rMap,
      nowIndex,
      go
    }
  }
})
