import { throttle, findHypotenuse, dispatchEvent } from './utils'

enum KeyCode {
  UP = 38,
  DOWN = 40,
  LEFT = 37,
  RIGHT = 39,
  ENTER = 13
}

enum Direction {
  UP_DOWN = 1,
  LEFT_RIGHT = 0,
  ALL = 2
}

type RangeObj = Record<string, Record<string, number>>
type HandlerNameType = 'handlerUpDown' | 'handlerLeftRight'

class FocusableCore {
  focusClassName: string
  focusedAttrname: string
  focusItemAttrname: string
  scrollItemAttrname: string
  scrollEl: HTMLElement
  limitingEl: HTMLElement | undefined | null
  optionalDomMap: Map<RangeObj, HTMLElement>
  optionalRangeMap: Map<Record<string, number | boolean>, RangeObj>
  scrollTimer: number | undefined
  offsetDistance: number
  isLocalRolling: boolean
  distanceToCenter: boolean
  longPressFn?: (d: boolean, t: string) => void
  longPressTimer: number | undefined
  isLongPress: boolean
  constructor () {
    const uId = Math.ceil(10000 * Math.random())
    this.focusClassName = 'onFocus' //焦点class名称
    this.focusedAttrname = 'focused' //焦点属性名称
    this.focusItemAttrname = 'focusable' + uId //可聚焦属性名称
    this.scrollItemAttrname = 'focusable-scroll-' + uId //局部滚动属性名称
    this.scrollEl = document.documentElement || document.body.parentNode
    this.optionalDomMap = new Map() //可聚焦元素集合
    this.optionalRangeMap = new Map() //可聚焦元素筛选后集合
    this.offsetDistance = 30 //偏移量
    this.isLocalRolling = false //当前是否是局部滚动
    this.distanceToCenter = false //焦点是否居中,默认固定偏移量模式
    this.isLongPress = false //是否是长按事件
  }

  //获取窗口滚动数据
  private getScrollXY () {
    const scrollTop = window.pageYOffset || 0
    const scrollLeft = window.pageXOffset || 0
    return {
      scrollTop,
      scrollLeft
    }
  }

  //获取元素位置
  private getElementPosition (el: HTMLElement) {
    const elRect = el.getBoundingClientRect()
    const { scrollTop, scrollLeft } = this.getScrollXY()
    const positionX = elRect.left + scrollLeft
    const positionY = elRect.top + scrollTop
    const rangeX = {
      start: positionX,
      end: elRect.width + positionX
    }
    const rangeY = {
      start: positionY,
      end: elRect.height + positionY
    }
    return {
      rangeX,
      rangeY
    }
  }

  //获取焦点元素
  private getFocusEl () {
    return document.querySelector(`[${this.focusedAttrname}]`) as HTMLElement
  }

  //获取焦点坐标,相对整个文档
  private getElementFocusPosition () {
    return this.getElementPosition(this.getFocusEl())
  }

  //生成可聚焦元素Map
  private createFocusItemMap () {
    const optionalDom = (this.limitingEl
      ? this.limitingEl
      : document
    ).querySelectorAll<HTMLElement>(`[${this.focusItemAttrname}]`)
    optionalDom.forEach((item, index) => {
      item.getAttribute(this.focusedAttrname) === null &&
        this.optionalDomMap.set(this.getElementPosition(item), item)
    })
  }

  //清除数据
  private clearData () {
    this.optionalRangeMap.clear()
    this.optionalDomMap.clear()
  }

  //筛选最近的跳转点
  private handlerRecent (direction: number) {
    const optionalRangeKeys = [...this.optionalRangeMap.keys()] //全部符合的点
    const belongArr = optionalRangeKeys.filter(i => i.belong) //属于滚动模块的点
    const targetArr = belongArr.length !== 0 ? belongArr : optionalRangeKeys
    const overlapArr = targetArr.filter(i => i.overlap) //重叠的点，优先重叠里选择，没有重叠的话就全部里筛选
    const screeningArr = overlapArr.length !== 0 ? overlapArr : targetArr
    const minNum = Math.min(
      //最近值
      ...(screeningArr.map(i => i.hypotenuse) as number[])
    )
    for (const key of this.optionalRangeMap.keys()) {
      if (key.hypotenuse === minNum) {
        const selectedRange = this.optionalRangeMap.get(key)
        if (selectedRange) {
          const selectedEl = this.optionalDomMap.get(selectedRange)
          this.onFocus(selectedEl as HTMLElement, true, direction)
        }
        break
      }
    }
    this.clearData()
  }

  //上下键移动逻辑
  private handlerUpDown (isDown: boolean, type: string) {
    this.createFocusItemMap()
    const { rangeX, rangeY } = this.getElementFocusPosition()
    const { start: xFocusStart, end: xFocusEnd } = rangeX
    const { start: yFocusStart, end: yFocusEnd } = rangeY
    const focusCenterX = (xFocusEnd - xFocusStart) / 2 + xFocusStart
    //筛选符合跳转的元素
    for (const key of this.optionalDomMap.keys()) {
      const xKeyStart = key.rangeX.start
      const xKeyEnd = key.rangeX.end
      const yKeyStart = key.rangeY.start
      const yKeyEnd = key.rangeY.end
      //元素和焦点重叠
      const overlapXY =
        xKeyEnd > xFocusStart &&
        xKeyStart < xFocusEnd &&
        yKeyEnd > yFocusStart &&
        yKeyStart < yFocusEnd
      //向下，筛选目标上边Y值，大于焦点中心点Y值
      //向上，焦点中心点Y值，大于筛选目标下边Y值
      if (
        (isDown && yKeyStart > yFocusStart + (yFocusEnd - yFocusStart) / 2) ||
        (!isDown && yFocusStart + (yFocusEnd - yFocusStart) / 2 > yKeyEnd) ||
        overlapXY
      ) {
        const keyCenterX = (xKeyEnd - xKeyStart) / 2 + xKeyStart
        const keyCenterY = (yKeyEnd - yKeyStart) / 2 + yKeyStart
        const yGap = isDown ? keyCenterY - yFocusStart : yFocusEnd - keyCenterY
        const xGap = keyCenterX - focusCenterX
        const overlap = xKeyEnd > xFocusStart && xKeyStart < xFocusEnd //X轴上的重叠
        const hypotenuse = findHypotenuse(Math.abs(yGap), Math.abs(xGap))
        const belong = this.checkBelongCurrentScroll(
          this.optionalDomMap.get(key) as HTMLElement
        )
        const keyParams = {
          hypotenuse, //斜边距离
          overlap, //是否重叠
          belong //这个点是否属于当前滚动模块
        }
        this.optionalRangeMap.set(keyParams, key)
      }
    }
    dispatchEvent(this.getFocusEl(), { type })
    if (this.optionalRangeMap.size === 0) return this.clearData()
    dispatchEvent(this.getFocusEl(), { type: 'onBlur' })
    this.handlerRecent(1)
  }

  //左右键移动逻辑
  private handlerLeftRight (isRight: boolean, type: string) {
    this.createFocusItemMap()
    const { rangeX, rangeY } = this.getElementFocusPosition()
    const { start: xFocusStart, end: xFocusEnd } = rangeX
    const { start: yFocusStart, end: yFocusEnd } = rangeY
    const focusCenterY = (yFocusEnd - yFocusStart) / 2 + yFocusStart
    for (const key of this.optionalDomMap.keys()) {
      const xKeyStart = key.rangeX.start
      const xKeyEnd = key.rangeX.end
      const yKeyStart = key.rangeY.start
      const yKeyEnd = key.rangeY.end
      //元素和焦点重叠
      const overlapXY =
        xKeyEnd > xFocusStart &&
        xKeyStart < xFocusEnd &&
        yKeyEnd > yFocusStart &&
        yKeyStart < yFocusEnd
      //目标最左X要在焦点元素最左X右边,目标最下Y要在焦点元素最上Y下边
      if (
        (isRight && xKeyStart > xFocusStart + (xFocusEnd - xFocusStart) / 2) ||
        (!isRight && xFocusStart + (xFocusEnd - xFocusStart) / 2 > xKeyEnd) ||
        overlapXY
      ) {
        const keyCenterX = (xKeyEnd - xKeyStart) / 2 + xKeyStart
        const keyCenterY = (yKeyEnd - yKeyStart) / 2 + yKeyStart
        const xGap = isRight ? keyCenterX - xFocusStart : xFocusEnd - keyCenterX
        const yGap = keyCenterY - focusCenterY
        const overlap = yKeyEnd > yFocusStart && yKeyStart < yFocusEnd //Y轴上的重叠
        const hypotenuse = findHypotenuse(Math.abs(yGap), Math.abs(xGap))
        const belong = this.checkBelongCurrentScroll(
          this.optionalDomMap.get(key) as HTMLElement
        )
        const keyParams = {
          hypotenuse, //斜边距离
          overlap, //是否重叠
          belong //这个点是否属于当前滚动模块
        }
        this.optionalRangeMap.set(keyParams, key)
      }
    }
    dispatchEvent(this.getFocusEl(), { type })
    if (this.optionalRangeMap.size === 0) return this.clearData()
    dispatchEvent(this.getFocusEl(), { type: 'onBlur' })
    this.handlerRecent(0)
  }

  //设置元素滚动值
  private setScroll (
    x: number,
    y: number,
    animation: boolean,
    direction: number
  ) {
    const nowL = this.scrollEl.scrollLeft
    const nowT = this.scrollEl.scrollTop
    const stepX = animation ? (x - nowL) / 10 : 0
    const stepY = animation ? (y - nowT) / 10 : 0
    this.scrollTimer = setTimeout(() => {
      if (Math.abs(stepX) <= 1 && Math.abs(stepY) <= 1) {
        if (direction === Direction.LEFT_RIGHT || direction === Direction.ALL) {
          this.scrollEl.scrollLeft = x
        }
        if (direction === Direction.UP_DOWN || direction === Direction.ALL) {
          this.scrollEl.scrollTop = y
        }
        return
      }
      if (direction === Direction.LEFT_RIGHT || direction === Direction.ALL) {
        this.scrollEl.scrollLeft = nowL + stepX
      }
      if (direction === Direction.UP_DOWN || direction === Direction.ALL) {
        this.scrollEl.scrollTop = nowT + stepY
      }
      this.setScroll(x, y, animation, direction)
    }, 0)
  }

  //元素滚动
  private scroll (animation: boolean, direction: number) {
    clearTimeout(this.scrollTimer)
    const cW = this.scrollEl.clientWidth //视窗宽
    const cH = this.scrollEl.clientHeight //视窗高
    const sW = this.scrollEl.scrollWidth - cW //X可滚动距离
    const sH = this.scrollEl.scrollHeight - cH //Y可滚动距离
    if (sH <= 0 && sW <= 0) return //没有滚动条
    const { rangeX, rangeY } = this.getElementFocusPosition() //获取焦点坐标,相对整个文档
    const nowT = this.scrollEl.scrollTop //Y已滚动长度
    const nowL = this.scrollEl.scrollLeft //X已滚动长度
    const { rangeX: vX, rangeY: vY } = this.getElementPosition(this.scrollEl) //滚动视窗相对文档的位置
    //焦点元素Y值
    const bY = this.isLocalRolling ? rangeY.end - vY.start + nowT : rangeY.end
    const tY = this.isLocalRolling
      ? rangeY.start - vY.start + nowT
      : rangeY.start
    //焦点元素Y值
    const bX = this.isLocalRolling ? rangeX.end - vX.start + nowL : rangeX.end
    const tX = this.isLocalRolling
      ? rangeX.start - vX.start + nowL
      : rangeX.start

    //元素在窗口内
    if (
      tY - nowT >= 0 &&
      bY - nowT <= cH &&
      tX - nowL >= 0 &&
      bX - nowL <= cW &&
      !this.distanceToCenter
    )
      return

    //偏移量计算,包括居中偏移和固定量偏移
    const offsetDisX = this.distanceToCenter
      ? cW / 2 - (rangeX.end - rangeX.start) / 2
      : this.offsetDistance
    const offsetDisY = this.distanceToCenter
      ? cH / 2 - (rangeY.end - rangeY.start) / 2
      : this.offsetDistance

    //计算偏移距离
    let distX = bX - cW + offsetDisX
    let distY = bY - cH + offsetDisY

    if (tY - nowT < 0) {
      //向上滚动,超出
      distY = tY - offsetDisY
    }
    if (tX - nowL < 0) {
      //向左滚动
      distX = tX - offsetDisX
    }
    distX > sW && (distX = sW)
    distX < 0 && (distX = 0)
    distY > sH && (distY = sH)
    distY < 0 && (distY = 0)
    //修正滚动方向模式
    if (
      direction === Direction.LEFT_RIGHT &&
      !(tY - nowT >= 0 && bY - nowT <= cH)
    ) {
      direction = 2
    }
    if (
      direction === Direction.UP_DOWN &&
      !(tX - nowL >= 0 && bX - nowL <= cW)
    ) {
      direction = 2
    }
    this.setScroll(distX, distY, animation, direction)
  }

  //长按
  private longPress = () => {
    dispatchEvent(this.getFocusEl(), { type: 'longPress' })
    this.longPressTimer = 0
    this.isLongPress = true
  }

  onFocus (el: Element, animation = false, direction = Direction.ALL) {
    if (!el) return
    const curEl = this.getFocusEl()
    // curEl.setAttribute('class', className + ' ' + this.focusClassName)
    if (curEl) {
      curEl.removeAttribute(this.focusedAttrname)
      curEl.classList.remove(this.focusClassName)
    }
    //聚焦
    const className = el.getAttribute('class') || ''
    el.setAttribute(this.focusedAttrname, '')
    el.setAttribute('class', className + ' ' + this.focusClassName)
    dispatchEvent(this.getFocusEl(), { type: 'onFocus' })
    //焦点在哪里？局部滚动上，还是浏览器窗口上
    this.tabScrollEl()
    //滚动
    this.scroll(animation, direction)
  }

  //方向键长按交互函数
  private handleDirection = (
    directionType: boolean,
    direction: string,
    handlerName: HandlerNameType
  ) => {
    if (!this.longPressTimer) {
      this[handlerName](directionType, direction)
      this.longPressFn = throttle(this[handlerName], 500)
    }
    this.longPressTimer = setTimeout(() => {
      !!this.longPressFn && this.longPressFn(directionType, direction)
    })
  }

  //频道点是否属于滚动模块
  private checkBelongCurrentScroll = (el: HTMLElement) => {
    let optionalDom: NodeListOf<HTMLElement> | null = null
    let belong = false
    optionalDom = document.querySelectorAll<HTMLElement>(
      `[${this.scrollItemAttrname}]`
    )
    optionalDom.forEach(item => {
      item.contains(this.getFocusEl()) && item.contains(el) && (belong = true)
    })
    optionalDom = null
    return belong
  }

  //切换滚动目标，局部滚动还是浏览器窗口滚动
  private tabScrollEl () {
    let optionalDom: NodeListOf<HTMLElement> | null = null
    optionalDom = document.querySelectorAll<HTMLElement>(
      `[${this.scrollItemAttrname}]`
    )
    optionalDom.forEach((item, index) => {
      this.isLocalRolling = item.contains(this.getFocusEl())
      this.scrollEl = this.isLocalRolling
        ? item
        : document.documentElement || document.body.parentNode
    })
    optionalDom = null
  }

  //按键按下
  onKeyDownEvent (evt: KeyboardEvent) {
    evt.preventDefault()
    evt.stopPropagation()
    switch (evt.keyCode) {
      case KeyCode.UP:
        this.handleDirection(false, 'up', 'handlerUpDown')
        break
      case KeyCode.DOWN:
        this.handleDirection(true, 'down', 'handlerUpDown')
        break
      case KeyCode.LEFT:
        this.handleDirection(false, 'left', 'handlerLeftRight')
        break
      case KeyCode.RIGHT:
        this.handleDirection(true, 'right', 'handlerLeftRight')
        break
      case KeyCode.ENTER:
        !this.longPressTimer &&
          !this.isLongPress &&
          (this.longPressTimer = setTimeout(this.longPress, 500))
        break
      default:
        break
    }
  }

  //按键抬起
  onKeyUpEvent (evt: KeyboardEvent) {
    clearTimeout(this.longPressTimer)
    switch (evt.keyCode) {
      case KeyCode.ENTER:
        !this.isLongPress && dispatchEvent(this.getFocusEl(), { type: 'click' })
        this.isLongPress && (this.isLongPress = false)
        break
      default:
        break
    }
    this.longPressTimer = 0
    this.longPressFn = undefined
  }

  //设置焦点限制区域
  setLimitingEl (e: HTMLElement) {
    this.limitingEl = e
  }
  //重置焦点限制区域
  resetLimitingEl () {
    this.limitingEl = null
  }
}

export default FocusableCore
