//节流函数
export const throttle = (fn: any, delay: number) => {
  let prev = Date.now()
  return function (this: any, ...args: any) {
    const now = Date.now()
    if (now - prev >= delay) {
      fn.apply(this, args)
      prev = Date.now()
    }
  }
}

//三角形求斜边长
export const findHypotenuse = (base: number, perpendicular: number) => {
  const bSquare = base ** 2
  const pSquare = perpendicular ** 2
  const sum = bSquare + pSquare
  const hypotenuse = Math.sqrt(sum)
  return hypotenuse
}

//自定义事件
interface Payload {
  type: string
  bubbles?: boolean
  cancelable?: boolean
}
const isNull = (any: any) => typeof any === 'undefined' || any === null
export const dispatchEvent = (
  el: HTMLElement,
  payload: Payload,
  Event = window.Event
) => {
  const { type, bubbles = false, cancelable = false, ...data } = payload
  let event
  if (!isNull(Event)) {
    event = new Event(type, { bubbles, cancelable })
  } else {
    event = document.createEvent('HTMLEvents')
    event.initEvent(type, bubbles, cancelable)
  }
  Object.assign(event, data)
  return el.dispatchEvent(event)
}
